@import url(https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400;500;600;700&display=swap);
* {
  margin: 0;
}

html {
  --clr-primary: rgb(0, 205, 219);
  --clr-gray-500: #4e4e4e;
  --clr-gray-400: #252525;

  --clr-block: rgb(0, 152, 240);
  --clr-selected: rgb(255, 115, 0);

  --header-height: 8rem;
}

body {
  font-family: "Fira Code", monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--clr-gray);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: var(--clr-gray-400);
}

:root {
  --header-border-width: 7px;
}

.header {
  background: linear-gradient(
    90deg,
    rgb(176, 255, 102) 0%,
    rgb(118, 216, 255) 100%
  );
  min-height: calc(var(--header-height) - 7px);
  min-height: calc(var(--header-height) - var(--header-border-width));
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  border-bottom: 7px solid var(--clr-gray-500);
  border-bottom: var(--header-border-width) solid var(--clr-gray-500);
}

.header__title {
  font-size: 1.5rem;
}

.header__logo-container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  grid-column-gap: 0.5rem;
  -webkit-column-gap: 0.5rem;
          column-gap: 0.5rem;
  color: var(--clr-gray-500);
}

.header__algo-icon {
  font-size: 4.75rem !important;
  margin-bottom: -6px;
}

.header__actions {
  display: none;
  position: fixed;
  background: linear-gradient(
    120deg,
    rgb(151, 226, 255) 0%,
    rgb(192, 255, 110) 100%
  );
  box-shadow: 0 0 20px black;
  border-left: 5px solid var(--clr-gray-500);
  border-bottom: 5px solid var(--clr-gray-500);
  border-radius: 0 0 0 10px;
  -webkit-flex-direction: column;
          flex-direction: column;
  top: 0;
  right: 0;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  grid-row-gap: 1rem;
  row-gap: 1rem;
  padding: 2rem;
  z-index: 3;
}

.header__menu-heading {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.header__menu-title {
  font-size: 1.25rem;
  font-weight: 500;
  color: var(--clr-gray-400);
}

.header__actions--visible {
  display: -webkit-flex;
  display: flex;
}

.header__algo-selection {
  width: 100%;
}

.header__start-button {
  width: 100%;
}

@media only screen and (min-width: 950px) {
  .header__start-button {
    width: auto;
  }

  .header__algo-selection {
    width: 175px;
  }

  .header__menu-button {
    display: none !important;
  }

  .header__actions {
    box-shadow: none;
    border: none;
    height: inherit;
    display: -webkit-flex;
    display: flex;
    position: inherit;
    -webkit-flex-direction: row;
            flex-direction: row;
    grid-gap: 1.5rem;
    gap: 1.5rem;
    background: none;
    -webkit-align-items: center;
            align-items: center;
  }

  .header__menu-heading {
    display: none;
  }
}

.sliders__wrapper {
  width: 200px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
}

.sliders__container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
}

.array-container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  -webkit-justify-content: center;
          justify-content: center;
  min-height: 10rem;
  height: 100%;
  padding: 2rem 0 3rem 0;
  background-color: var(--clr-gray-400);
}

.array-item {
  position: relative;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  margin: 0 0.5rem;
  width: 100%;
  background-color: var(--clr-block);
  transition: background-color 150ms ease-in;
  transition: height 100ms ease-in;
  border-radius: 2px 2px 0 0;
}

.array-item--selected {
  background-color: var(--clr-selected);
}

.array-item:nth-child(2n) > .array-item__text {
  text-decoration: underline;
}

.array-item__text {
  position: absolute;
  color: white;
  margin-top: 1rem;
  font-weight: 700;
  bottom: -1.5rem;
  font-size: 1rem;
}

@media only screen and (min-width: 1000px) {
  .array-item__text {
    bottom: -2rem;
    font-size: 1.5rem;
  }
}

